/* global __API__:true */
import store from '../app/appStore';

class Api {
  static headers(session) {
    let headers = {
      Accept: 'application/json, text/plain, */*',
      'Content-Type': 'application/json',
      dataType: 'json',
      Cookie: '_ga=GA1.2.270328270.1538406437; _gid=GA1.2.420376681.1538406455; __sbzid=pzg1es7fogo3w2hmt7l1532503421957; __utmc=108966807; __utmz=108966807.1538484074.3.3.utmcsr=google|utmccn=(organic)|utmcmd=organic|utmctr=(not%20provided); __sbzsid=GfNP7TFiFik24wD4EnlqST0m; _gat=1; __utma=108966807.270328270.1538406437.1538491446.1538498446.5; __utmb=108966807.1.9.1538498446',
      Origin: 'http://www.vetau.com.vn',
      'Accept-Encoding': 'gzip, deflate, br',
      'Accept-Language': 'vi-VN,vi;q=0.9,en-US;q=0.8,en;q=0.7',
      // ClientID: '9981020e848a4123bb530623ec92c5d2',
      Referer: 'http://www.vetau.com.vn',
      ClientLang: 'vi',
      'User-Agent': 'Mozilla/5.0 (Macintosh; Intel Mac OS X 10_13_6) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/69.0.3497.100 Safari/537.36',
    };

    if (session && session.token) {
      headers = Object.assign(headers, {
        Authorization: `Bearer ${session.token}`,
      });
    }

    if (session && session.uid) {
      if (session.uid) {
        headers = Object.assign(headers, {
          UID: session.uid,
        });
      }
      if (session.clientID) {
        headers = Object.assign(headers, {
          ClientID: session.clientID,
        });
      }
    }
    return headers;
  }

  static get(route, headers) {
    return this.xhr(route, null, headers, 'GET');
  }

  static put(route, params, headers) {
    return this.xhr(route, params, headers, 'PUT');
  }

  static post(route, params, headers, isEncodeData, isFormData) {
    return this.xhr(route, params, headers, 'POST', isEncodeData, isFormData);
  }

  static delete(route, params, headers) {
    return this.xhr(route, params, headers, 'DELETE');
  }

  static encodeData(data) {
    return Object.keys(data).map(key => [key, data[key]].map(encodeURIComponent).join('=')).join('&');
  }

  static xhr(route, params, headers, verb, isEncodeData, isFormData) {
    const host = __API__;
    const state = store.getState();
    let url = '';
    if (route.indexOf('http') !== -1) {
      url = route;
    } else {
      url = `${host}${route}`;
    }
    const formData = isFormData ? params : JSON.stringify(params);
    const options = Object.assign(
      { method: verb },
      params ? { body: isEncodeData ? this.encodeData(params) : formData } : null);
    options.headers = Api.headers(state.session);
    if (headers) {
      options.headers = Object.assign({}, options.headers, headers);
    }
    return fetch(url, options).then((resp) => {
      const json = resp.json();
      if (resp.ok) {
        return json;
      }
      return json.then((err) => { throw err; });
    }).then(json => json);
  }

}
export default Api;
