import createReducer from '../utils/createReducer';
import * as types from './appActionTypes';

const _ = require('lodash');

export const session = createReducer(null, {
  [types.SET_SESSION](state, action) {
    const newState = state ? _.cloneDeep(state) : {};
    Object.assign(newState, action.data);
    return newState;
  },
});

export const availableTickets = createReducer([], {
  [types.SET_AVAILABLE_TICKETS](state, action) {
    return action.data;
  },
  [types.ADD_AVAILABLE_TICKET](state, action) {
    if (state.findIndex(e => e.Id === action.data.Id) >= 0) {
      return state;
    }
    const newState = state.slice();
    newState.push(action.data);
    /* const { trip, ticket } = action.data;
    const foundTrip = newState.find(e => e.Id === trip.Id);
    if (foundTrip === undefined) {
      trip.availableTickets = [ticket];
      newState.push(trip);
    } else {
      foundTrip.availableTickets.push(ticket);
    } */
    return newState;
  },
});
