import Api from '../utils/api';

// const _ = require('lodash');

export function fetchAllTrips(date) {
  const body = {
    1: 'SGO',
    2: 'NTR',
    3: date,
    4: date,
    5: true,
    6: '',
  };
  return Api.post('/api/banveweb/SearchTauByGaDiGaDenNgayXP', body, null);
}

export function fetchAllTickets(tripId, coachId, code) {
  const body = {
    1: tripId,
    2: coachId,
    3: code,
  };
  return Api.post('/api/BanVeWeb/GetChosByToaXeId', body, null);
}
