class Home extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      result: [],
    };
    this.handleOnChangeDate = this.handleOnChangeDate.bind(this);
    this.handleOnChangeToken = this.handleOnChangeToken.bind(this);
    this.handleOnChangeUID = this.handleOnChangeUID.bind(this);
    this.handleOnChangeClientID = this.handleOnChangeClientID.bind(this);
    this.handleOnClickButton = this.handleOnClickButton.bind(this);
  }

  async getFreeTickets(date) {
    const resp = await appServices.fetchAllTrips(date);
    for (const trip of resp.TauDis) {
      for (const coach of trip.ToaXes.filter(e => e.ToaXeStatus === 1 && (e.NhomChoWeb === 'NAM' || e.NhomChoWeb === 'NAC'))) {
        // console.log(coach);
        appServices.fetchAllTickets(trip.Id, coach.Id, resp.BookingCode)
          .then((allTickets) => {
            allTickets.Chos.filter(e => e.LoaiCho.indexOf('T1') >= 0 && (e.Status.Status === 2 || e.Status.Status === 3)).forEach((ticket) => {
              this.props.addAvailableTicket(trip, ticket);
            });
          });
        // console.log(allTickets);
      }
    }
  }

  handleOnChangeToken(event) {
    this.props.setSession({
      token: event.target.value,
    });
  }

  handleOnChangeUID(event) {
    this.props.setSession({
      uid: event.target.value,
    });
  }

  handleOnChangeClientID(event) {
    this.props.setSession({
      clientID: event.target.value,
    });
  }

  handleOnChangeDate(event) {
    this.props.setSession({
      date: event.target.value,
    });
  }

  handleOnClickButton() {
    console.log('handleOnClickButton');
    this.props.setAvailableTickets([]);
    this.getFreeTickets(_.get(this.props.session, 'date', '2019-02-01'));
  }

  renderTickets() {
    return this.props.availableTickets.sort((a, b) => a.Status.Duration - b.Status.Duration).map(e => (
      <div style={{ margin: 10, display: 'inline-block', width: '30%', height: 250, border: '1px solid #bbb', textAlign: 'center' }}>
        <p>TicketId: <b>{e.Status.TicketId}</b></p>
        <p>MacTau: <b>{e.MacTau}</b></p>
        <p>NgayGioDi: <b>{e.NgayGioDi}</b></p>
        <p>LoaiCho: <b>{e.LoaiCho}</b></p>
        <p>ToaSo: <b>{e.ToaSo}</b></p>
        <p>Available at: <b>{e.Status.availableAt}</b></p>
      </div>
    ));
  }

  render() {
    // console.log(this.props.availableTickets);
    const token = _.get(this.props.session, 'token', '');
    const uid = _.get(this.props.session, 'uid', '');
    const clientID = _.get(this.props.session, 'clientID', '');
    const date = _.get(this.props.session, 'date', '2020-01-20');
    return (
      <div className="home">
        <TextField
          label="Token"
          margin="normal"
          variant="outlined"
          fullWidth
          multiline
          rows={5}
          value={token}
          onChange={this.handleOnChangeToken}
        />
        <TextField
          label="UID"
          margin="normal"
          variant="outlined"
          fullWidth
          value={uid}
          onChange={this.handleOnChangeUID}
        />
        <TextField
          label="ClientID"
          margin="normal"
          variant="outlined"
          fullWidth
          value={clientID}
          onChange={this.handleOnChangeClientID}
        />
        <FormControl component="fieldset">
          <FormLabel component="legend">Date</FormLabel>
          <RadioGroup
            aria-label="Date"
            name="date"
            value={date}
            onChange={this.handleOnChangeDate}
          >
            <FormControlLabel value="2020-01-20" control={<Radio />} label="2020-01-20" />
            <FormControlLabel value="2020-01-21" control={<Radio />} label="2020-01-21" />
            <FormControlLabel value="2020-01-22" control={<Radio />} label="2020-01-22" />
            <FormControlLabel value="2020-01-23" control={<Radio />} label="2020-01-23" />
            <FormControlLabel value="2020-01-24" control={<Radio />} label="2020-01-24" />
            <FormControlLabel value="2020-01-25" control={<Radio />} label="2020-01-25" />
          </RadioGroup>
        </FormControl>
        <Button variant="contained" color="primary" fullWidth onClick={this.handleOnClickButton}>
          Find Tickets
        </Button>
        <br />
        {this.renderTickets()}
      </div>
    );
  }

}

Home.propTypes = {
  session: PropTypes.instanceOf(Object),
  availableTickets: PropTypes.instanceOf(Array),
  setSession: PropTypes.func.isRequired,
  setAvailableTickets: PropTypes.func.isRequired,
  addAvailableTicket: PropTypes.func.isRequired,
};

Home.defaultProps = {
  session: null,
  availableTickets: [],
};

function mapStateToProps(state) {
  return {
    session: state.session,
    availableTickets: state.availableTickets,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(Object.assign({}, { setSession, setAvailableTickets, addAvailableTicket }), dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
