import { Component } from 'react';
import PropTypes from 'prop-types';
import ReactFauxDom from 'react-faux-dom';
import { select } from 'd3-selection';
import cloud from 'd3-cloud';
import { scaleLinear } from 'd3-scale';

const LinearScale = require('linear-scale');


// const fill = scaleOrdinal(schemeCategory10);

const defaultClickEvent = (word) => {
  // eslint-disable-next-line no-console
  console.log(word);
};

class WordCloud extends Component {
  constructor(props) {
    super(props);
    this.state = {
      max: 3,
      increaseNumber: 0,
    };
  }

  componentWillMount() {
    this.wordCloud = ReactFauxDom.createElement('div');
  }

  computeLayout() {
    const { data, width, height, padding, font, rotate, onWordClick } = this.props;
    const wordCounts = data.map(
      text => ({ ...text }),
    );

    const scale = scaleLinear().domain([0, this.state.max]).range([14, 60]);
    const fontSizeMapper = word => scale(word.value);

    console.log(this.state.max, wordCounts);

    // render based on new data
    const layout = cloud()
      .size([width, height])
      .font(font)
      .words(wordCounts)
      .padding(padding)
      .rotate(rotate)
      .fontSize(fontSizeMapper)
      .random(() => 0.5)
      .on('end', (words) => {
        console.log(words.length, data.length);
        if (words.length < data.length && this.state.increaseNumber < 100) {
          this.setState({
            increaseNumber: this.state.increaseNumber + 1,
            max: this.state.max + 1,
          });
        } else {
          select(this.wordCloud)
            .append('svg')
            .attr('width', layout.size()[0])
            .attr('height', layout.size()[1])
            .append('g')
            .attr('transform', `translate(${layout.size()[0] / 2},${layout.size()[1] / 2})`)
            .selectAll('text')
            .data(words)
            .enter()
            .append('text')
            .style('font-size', d => `${d.size}px`)
            .style('font-weight', 'bold')
            .style('font-family', font)
            .style('fill', () => '#222')
            // .style('fill', (d, i) => fill(i))
            .attr('text-anchor', 'middle')
            .attr('transform',
              d => `translate(${[d.x, d.y]})rotate(${d.rotate})`,
            )
            .text(d => d.text)
            .on('click', d => onWordClick(d));
        }
      });

    layout.start();
  }

  render() {
    // clear old words
    select(this.wordCloud).selectAll('*').remove();
    this.computeLayout();
    return this.wordCloud.toReact();
  }
}

WordCloud.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    text: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
  })).isRequired,
  width: PropTypes.number,
  height: PropTypes.number,
  padding: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.func,
  ]),
  font: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.func,
  ]),
  // fontSizeMapper: PropTypes.func,
  onWordClick: PropTypes.func,
  rotate: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.func,
  ]),
};

WordCloud.defaultProps = {
  width: 500,
  height: 500,
  padding: 15,
  font: 'Helvetica',
  fontSizeMapper: word => word.value,
  rotate: 0,
  onWordClick: defaultClickEvent,
};


export default WordCloud;
