import * as types from './appActionTypes';

const moment = require('moment');

function getAvailabeTime(restCooldown) {
  if (restCooldown === 0) {
    return 'Now';
  }
  const date = new Date((moment().unix() + restCooldown) * 1000);
  return moment(date).format('MMMM Do YYYY, h:mm:ss a');
}

export function setSession(session) {
  return { type: types.SET_SESSION, data: session };
}

export function setAvailableTickets(data) {
  return { type: types.SET_AVAILABLE_TICKETS, data: data };
}

export function addAvailableTicket(trip, ticket) {
  const data = Object.assign({}, ticket, {
    MacTau: trip.MacTau,
    NgayGioDi: trip.NgayGioDi,
  });
  if (data.Status.Duration === null) {
    data.Status.Duration = 0;
  }
  data.Status.availableAt = getAvailabeTime(data.Status.Duration);
  return { type: types.ADD_AVAILABLE_TICKET, data: data };
}
